/* Menu Navigation ------------------------------------------------------------------------------------ */
.main-menu{
  width: 100%;
  position: absolute;
  top: 60px;
  left: 0;
  display: inline-flex;
  align-items: center;
}
.logo-menu{
  max-height: 90px;
  width: 250px;
  position: absolute;
  left: 0;
  cursor: pointer;
}
.menu-items{
  margin: 0 auto;
}
.itemsMenu{
  color: #fff;
  padding: 10px;
  font-size: 15px;
  text-decoration: none;
  border-bottom: 1px solid #fff;
  transition: 0.4s all;
}
.itemsMenu:hover{
  transform: scale(1.2);
  border-bottom: 1px solid #F46309;
}
.profile-items{
  max-height: 50px;
  width: 50px;
  border-radius: 9px;
  background-color: #1A1A1A;
  position: absolute;
  top: -10px;
  left: 93%;
  transition: 0.4s all;
}
.profile-icon{
  max-height: 50px;
  width: 50px;
  cursor: pointer;
  transition: 0.4s all;
  border-radius: 9px;
}

.profile-items:hover {
  border-radius: 9px 9px 0 0;
}

.profile-items:hover .dropMenu{
  display: block;
  transition: 0.4s all;
}
.dropMenu{
  display: none;
  position: relative;
  left: -50px;
  width: 100px;
  transition: 0.4s all;
}
.dropMenu-items{
  margin-top: 0px;
  list-style: none;
  border-radius: 5px;
}
.dropMenu-btn-li{
  margin-left: -39px;
  padding-top: 7px;
  background-color: #1A1A1A;
  align-content: center;
  width: 100px;
  height: 30px;
  cursor: pointer;
  transition: 0.4s all;
}
.dropMenu-items > li:hover{
  background-color: #5c5c5c;
}
.dropMenu-btn-li1{
  border-radius: 10px 0 0 0;
}
.dropMenu-btn-li2{
  border-radius: 0 0 10px 10px;
}
.dropMenu-btn{
  font-size: 15px;
  width: 100px;
  color: #fff;
  text-decoration: none;
  width: 100px;
  height: 20px;
}
